import axios from "axios";

const baseURL = "https://trevtrinh.com";
const urlAuth = baseURL + "/auth";
const urlLink = {
  create: baseURL + "/link/create/",
};

export const validateLogin = async (password) => {
  const res = await axios.post(urlAuth, { password: password });
  return res.data.validated;
};

export const createLink = async (accessName, redirectLink) => {
  const res = await axios.post(urlLink.create, {
    accessName: accessName,
    redirectLink: redirectLink,
  });
  return res.data;
};

export const fetchLink = async (accessName) => {
  const res = await axios.get(baseURL + "/" + accessName);
  return res;
};
